/* Sidebar.css */

.sidebar {
    background-color: #222;
    color: #fff;
    width: 200px;
    height: 100%;
    padding: 1rem;
  }
  
  .sidebar nav ul {
    list-style: none;
    padding: 0;
  }
  
  .sidebar nav li {
    margin-bottom: 1rem;
  }
  
  .sidebar nav a {
    text-decoration: none;
    color: #fff;
    font-size: 1rem;
    transition: color 0.3s ease;
  }
  
  .sidebar nav a:hover {
    color: #ff5733; /* Change color on hover */
  }
  