/* Home.css */

.home {
    text-align: center;
    padding: 2rem;
    background-color: #f4f4f4;
    border-radius: 5px;
  }
  
  .home h1 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .home p {
    font-size: 1.2rem;
    color: #666;
    margin-bottom: 2rem;
  }
  
  .home .buttons {
    display: flex;
    justify-content: center;
  }
  
  .home a {
    text-decoration: none;
    background-color: #007BFF;
    color: #fff;
    padding: 1rem 2rem;
    border-radius: 5px;
    font-size: 1.2rem;
    margin: 0 1rem;
    transition: background-color 0.3s ease;
  }
  
  .home a:hover {
    background-color: #0056b3; /* Change color on hover */
  }
  